/* Header.css */

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 20px;
    /* background-color: #f9f5c2; */
    color: white;

}

.logo img {
    max-width: 100px;
    width: 100px;
    /* Adjust the logo size as needed */
}

.nav .rolling-content {
    display: block;
    width: 600px;
    height: 30px;
    /* justify-content: space-between; */
    align-items: center;
    padding: 20px;
    /* background-color: #e9f2d7; */
    border: 2px dashed black;
    border-radius: 80px;
    color: rgb(0, 0, 0);
    font-weight: bold;
    font-size: larger;
    overflow-x: auto;
    overflow-y: hidden;
    white-space: nowrap;
    -webkit-overflow-scrolling: touch;
    scrollbar-width: none;
    /* Firefox */
    -ms-overflow-style: none;


    /* IE 10+ */
}

.nav .rolling-content p {
    text-align: center;
    display: flex;
    /* justify-content: space-between; */
    margin: 0;
    padding: 0;
    /* background-color: #e9f2d7; */

    color: rgb(0, 0, 0);
    font-weight: bold;
    font-size: 18px;
    overflow-x: auto;
    font-family: "Quicksand", sans-serif;
}

.nav ul {
    list-style: none;
    display: flex;
    gap: 100px;
}

.nav li {
    font-size: 18px;
}

.nav a {
    text-decoration: none;
    color: rgb(14, 14, 14);
}

.nav a:hover {
    text-decoration: none;
}