.main-projects-container .title-wrapper {
    display: flex;
    align-items: center;
    padding: 20px;
    background-color: #f3f3f2;
    border-radius: 15px;

}

.main-projects-container .title-wrapper h1 {

    margin-right: auto;
    color: #20294f;
    font-family: 'Quicksand', sans-serif;

}


.main-projects-container .portfolio {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(30%, 1fr));
    gap: 0px;
    padding: 20px;

}

.main-projects-container .portfolio-item {
    background-color: #fff;
    border: 2px solid #000000;
    padding: 10px;
    text-align: left;
    border-radius: 15px;
    transition: transform 0.2s ease-in-out;
    background-color: #ffffff;
    text-decoration: none;
}

.main-projects-container .portfolio-item:hover {
    transform: scale(1.05);
    /* border: 4px dotted #000000; */
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    /* Add a hover effect */
}


.main-projects-container .portfolio-item img {
    display: flex;
    max-width: 100%;

    height: 200px;
    border-radius: 10px 10px 0px 0px;
    margin-bottom: 10px;
    object-fit: cover;
    object-position: center;


}

/* section */
.main-projects-container .main-projects {
    display: block;
    justify-content: center;
    align-items: center;
    margin-top: 80px;
    width: 90%;
    /* background-color: #f3f3f2; */
    border-radius: 15px;
    padding: 20px;
}

.main-projects-container .main-projects h1 {
    text-align: left;
    font-size: 4rem;
    font-weight: 800;
    margin-bottom: 0px;
    margin-top: 0px;
    color: #20294f;

    /* font-family: 'Quicksand', sans-serif; */
    font-family: 'Inconsolata', monospace;
}

.main-projects-container .portfolio-item h3 {
    font-size: 1.5rem;
    margin-top: 10px;
    margin-bottom: -10px;
    padding: 8px 15px;
    background-color: #f9f5c2;
    color: #20294f;

    font-family: 'Inconsolata', monospace;

}

.main-projects-container .portfolio-item p {
    padding-left: 15px;
    font-size: 1rem;
    color: #777;

    font-family: 'Inconsolata', monospace;

}

.main-projects-container .portfolio-item .image-wrapper {
    width: 100%;
    text-align: center;
    font-size: 1rem;
    color: #777;
    height: 200px;

}


.main-projects-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 0 0px 0px;
    /* max-height: 900px; */
    font-family: 'Quicksand', sans-serif;
    background-color: #fffffc;
}