.about-section {
    align-items: center;
    font-family: 'Quicksand', sans-serif;

}

.title {
    font-size: 30px;
    font-weight: 600;
    margin: 10px;
    display: block;
    text-align: center;
}

.about-image {
    width: 100px;
    margin: 30px;
    padding-right: 20px;
    border-right: 1px dashed black;
}

.about-content {
    display: flex;
    align-items: center;
    padding: 0px 15%;
    max-width: 70%;


}

.horizontal-line {
    border-top: 1px dashed black;
    max-width: 60%;
    margin: 0px 20%;
}