/*for sidebar.js*/

.sidebar {
    display: flex;
    flex-direction: column;
    /* float: left; */
    width: 20%;
    height: auto;
    background-color: #f3f3f2;

    margin-right: 30px;
    border-radius: 15px;
    /* position: webkit-sticky; */
    /*border-right: 1px solid black;
    background-image: url("../public/grids.svg");
    background-size: 25px; */
}

.sidebar li {
    list-style-type: none;
    padding: 10px;
    margin: 10px;
    border: 1px dashed black;
    border-radius: 5px;
    background-color: #f3f3f2;
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    color: black;
    text-decoration: none;
    cursor: pointer;
}

.sidebar summary {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 20px 10px;
    font-weight: bold;
    font-size: auto;
    text-align: center;
    border: 0px solid #20294f;
    cursor: pointer;
}