/* styles.css */

/* Apply styles to the whole page */
/* body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
} */

/* Style the portfolio page container */
.portfolio-page {
    max-width: 900px;
    margin: 0 auto;
    padding: 20px;
    box-sizing: border-box;
}

/* Style the page title */
.portfolio-page h1 {
    font-size: 36px;
    margin-top: 0;
    text-align: left;
}

/* Style the introduction section */
.portfolio-page .introduction {
    margin-bottom: 20px;
    display: flex;
    /* text-align: left; */
    justify-content: space-between;
    align-items: flex-start;
}

.portfolio-page .introduction p {
    font-style: italic;
    /* line-height: 1.5; */
    /* display: flex; */
}

/* Style the cover image */
.portfolio-page img {
    display: block;
    margin: 0 auto;
    max-width: 100%;
    height: auto;
}

/* Style the detailed section */
.portfolio-page .detailed-section {
    background-color: #f5f5f5;
    padding: 20px;
    margin-top: 20px;
    border-radius: 5px;
    margin-bottom: 50px;
}

/* Style the grid section for images */
.portfolio-page .image-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    grid-gap: 10px;
    margin-top: 20px;
}

.portfolio-page .grid-image {
    max-width: 100%;
    height: auto;
    border-radius: 5px;
}

/* Media queries for responsiveness */
@media (max-width: 600px) {

    /* Adjust font size for smaller screens */
    .portfolio-page h1 {
        font-size: 28px;
    }

    /* Reduce padding for smaller screens */
    .portfolio-page .portfolio-page {
        padding: 10px;
    }
}