.projectOverview {

    display: block;
    justify-content: center;
    align-items: center;
    margin: 0 0 50px 0px;
    font-family: 'Quicksand', sans-serif;
    max-width: 1200px;
    margin: 0 auto;
}

.projectOverview .projectGallery {
    /* Images on the left */
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 20px 30px;

}

.projectOverview h1 {
    font-size: 2.5rem;
    margin-bottom: 20px;
    margin-top: 7%;
    color: #20294f;
    text-align: center;
}

.projectOverview .text1 {
    margin-top: 20px;
    margin-left: 20px;
    margin-right: 20px;
    align-items: center;
    justify-content: center;
    display: flex;
}

.projectOverview p {
    width: 80%;
    font-weight: 600;
    font-size: medium;
}

.projectOverview iframe {
    width: 80%;
    height: 400px;

    object-fit: cover;
    object-position: center;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    transition: all 0.3s ease-in-out;
}

.projectOverview img {
    width: 50%;
    height: auto;
    max-height: 400px;
    object-fit: cover;
    object-position: center;
    /* border-radius: 10px; */
    /* box-shadow: 0 0 10px rgba(0, 0, 0, 0.2); */
    transition: all 0.3s ease-in-out;
    margin: 0 20px;
}