/* WelcomeSection.css */
.welcome-section {
    display: flex;
    justify-content: center;
    align-items: center;
    height: auto;
    background-image: url("../public/background.png");
    /* background-color: #f0f2dd; */
    /* background-color: #2f93b8; */
    background-size: 300px;

    padding: 30px;
    margin: 0;
}

.welcome-section .full-length-logo {
    max-width: 1200px;
    width: 750px;

}

.welcome-section .welcome-left {
    display: block;
    justify-content: center;
    padding: 0px 50px;
    /* flex-direction: column;*/
}

.welcome-section .welcome-left h1 {
    font-family: 'Inconsolata', monospace;
    padding-left: 20px;
    text-align: left;
    color: #20294f;
    font-size: 30px;
    /* flex-direction: column;*/
}

.welcome-section #welcome-right {
    display: block;

}